import { BrowserModule } from '@angular/platform-browser';
import { NgModule, Injectable, NO_ERRORS_SCHEMA } from '@angular/core';
import { NgxGalleryModule } from 'ngx-gallery';
import { SwiperModule, SwiperConfigInterface, SWIPER_CONFIG } from 'ngx-swiper-wrapper';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {MatFormFieldModule} from '@angular/material/form-field';
import {MatButtonModule,MatCheckboxModule,MatToolbarModule,MatInputModule,MatProgressSpinnerModule,MatCardModule,MatMenuModule, MatIconModule, MatDialogModule} from '@angular/material'
import { TaskComponent } from './task/task.component';
import { HttpClientModule } from '@angular/common/http';
import { ReactiveFormsModule } from '@angular/forms';

const DEFAULT_SWIPER_CONFIG: SwiperConfigInterface = {
  direction: 'horizontal',
  slidesPerView: 'auto'
};
@NgModule({
  declarations: [
    AppComponent,
    TaskComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    MatFormFieldModule,
    MatIconModule,
    MatDialogModule,
    HttpClientModule,
    NgxGalleryModule,
    NgxGalleryModule,
    ReactiveFormsModule
  ],
  providers: [    {
    provide: SWIPER_CONFIG,
    useValue: DEFAULT_SWIPER_CONFIG
  }],
  bootstrap: [AppComponent],
  schemas: [NO_ERRORS_SCHEMA]
})

@Injectable({
  providedIn: 'root'
})
export class AppModule { }
